.user-container {
    display: flex;
    height: 100vh;
    width: 100%;
    background-color: #2c2c2c;
  }
  
  .sidebar {
    width: 200px;
    background-color: #333;
    padding: 20px;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar ul li {
    padding: 10px 0;
    color: white;
    cursor: pointer;
  }
  
  .user-info {
    flex-grow: 1;
    padding: 20px;
    color: white;
  }
  